import React from "react";
import "./Header.css";

const Header = () => {
  return (
    <div className="connect-header-container has-text-centered">
      <h1 className="title">Connect</h1>
      <p className="subtitle is-6">Make new connections</p>
    </div>
  );
};

export default Header;



